import React, { useState } from "react";
import axios from "axios";
import moment from "moment";
import "moment/locale/fr";

const Article = ({ article }) => {
  const [editing, setEditing] = useState(false);
  const [edited, setEdited] = useState("");

  const handleEdit = (e) => {
    const data = {
      author: article.author,
      content: edited ? edited : article.content,
      date: article.date,
      updatedDate: Date.now(),
    };

    axios.put("http://localhost:3004/articles/" + article.id, data).then(() => {
      window.location.reload();
      setEditing(false);
    });
  };

  const handleDelete = () => {
    axios.delete("http://localhost:3004/articles/" + article.id).then(() => {
      window.location.reload();
      getData();
    });
  };

  return (
    <div className="article">
      <div className="card-header">
        <h3>{article.author}</h3>
        <em>Post le {moment(article.date).format("LLLL")}</em>
      </div>

      {editing ? (
        <textarea
          defaultValue={article.content}
          onChange={(e) => setEdited(e.target.value)}
        ></textarea>
      ) : (
        <p>{edited ? edited : article.content}</p>
      )}

      <div className="btn-container">
        {editing ? (
          <button onClick={handleEdit}>Valider</button>
        ) : (
          <button
            onClick={() => {
              setEditing(true);
            }}
          >
            Edit
          </button>
        )}

        <button onClick={handleDelete}>Supprimer</button>
      </div>
    </div>
  );
};

export default Article;
